import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import {
  Box,
  Typography,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@mui/material';
import * as XLSX from 'xlsx';

function AnalysisPage() {
  const location = useLocation();
  const { file } = location.state || { file: null }; // Obtn el archivo de la ubicacin

  const [agents, setAgents] = useState([]);

  useEffect(() => {
    if (file) {
      // Lgica para leer el archivo y extraer la columna "Agentes"
      const reader = new FileReader();
      reader.onload = (e) => {
        const data = new Uint8Array(e.target.result);
        const workbook = XLSX.read(data, { type: 'array' });
        const firstSheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[firstSheetName];
        
        // Convertir el contenido de la hoja a JSON
        const jsonData = XLSX.utils.sheet_to_json(worksheet);

        // Extraer los nombres de los agentes
        const agentNames = jsonData.map(item => item['Agentes']).filter(Boolean);
        setAgents(agentNames);
      };
      reader.readAsArrayBuffer(file);
    }
  }, [file]);

  return (
    <Box
      sx={{
        minHeight: '100vh',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        padding: 3,
        backgroundColor: 'rgba(255, 255, 255, 0.9)',
      }}
    >
      <Typography variant="h4" sx={{ marginBottom: 3, fontFamily: 'Poppins, sans-serif', fontWeight: 600 }}>
        Anlisis de Agentes
      </Typography>

      <TableContainer component={Paper} sx={{ maxWidth: 800, boxShadow: '0 8px 30px rgba(0, 0, 0, 0.2)', borderRadius: '15px' }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell sx={{ fontWeight: 'bold' }}>#</TableCell>
              <TableCell sx={{ fontWeight: 'bold' }}>Nombre del Agente</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {agents.length > 0 ? (
              agents.map((name, index) => (
                <TableRow key={index}>
                  <TableCell>{index + 1}</TableCell>
                  <TableCell>{name}</TableCell>
                </TableRow>
              ))
            ) : (
              <TableRow>
                <TableCell colSpan={2} align="center">No se encontraron agentes.</TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
}

export default AnalysisPage;