import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  Button,
  Snackbar,
  IconButton,
} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import { styled } from '@mui/material/styles';

// Estilo para el contenedor de la pgina
const HomeContainer = styled(Box)(({ theme }) => ({
  backgroundImage: `url(${process.env.PUBLIC_URL + '/background.jpeg'})`,
  backgroundSize: 'cover',
  height: '100vh',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  justifyContent: 'center',
  position: 'relative',
  filter: 'brightness(0.8)',
}));

// Estilo para la tarjeta principal
const MainCard = styled(Box)(({ theme }) => ({
  backgroundColor: 'rgba(255, 255, 255, 0.8)',
  padding: '20px',
  borderRadius: '15px',
  boxShadow: '0 4px 20px rgba(0, 0, 0, 0.1)',
  textAlign: 'center',
  transition: 'transform 0.3s ease-in-out',
  '&:hover': {
    transform: 'scale(1.05)',
  },
}));

const HomePage = () => {
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: '' });
  const navigate = useNavigate();

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (file && file.name.endsWith('.xlsx')) {
      setSnackbar({ open: true, message: 'Archivo cargado correctamente.', severity: 'success' });

      // Navegar a la pgina de anlisis con el archivo
      navigate('/analysis', { state: { file } });
    } else {
      setSnackbar({ open: true, message: 'Por favor, sube un archivo .xlsx vlido.', severity: 'error' });
    }
  };

  const handleCloseSnackbar = () => {
    setSnackbar({ ...snackbar, open: false });
  };

  return (
    <HomeContainer>
      <MainCard>
        <img src={process.env.PUBLIC_URL + '/logo.svg'} alt="Logo de Clarence" style={{ width: '150px', animation: 'spin 5s linear infinite' }} />
        <Typography variant="h4" sx={{ marginBottom: 2, fontFamily: 'Poppins, sans-serif', fontWeight: 600 }}>
          Bienvenido a Clarence
        </Typography>
        <Typography variant="body1" sx={{ marginBottom: 3 }}>
          Cargue su archivo para comenzar el anlisis.
        </Typography>
        <input
          accept=".xlsx"
          id="file-upload"
          type="file"
          style={{ display: 'none' }}
          onChange={handleFileChange}
        />
        <label htmlFor="file-upload">
          <Button variant="contained" component="span" sx={{ backgroundColor: '#5e17eb', '&:hover': { backgroundColor: '#3c0fb4' } }}>
            Cargar Archivo
          </Button>
        </label>
      </MainCard>
      <Snackbar
        open={snackbar.open}
        autoHideDuration={3000}
        onClose={handleCloseSnackbar}
        message={snackbar.message}
        action={
          <React.Fragment>
            <IconButton size="small" aria-label="close" color="inherit" onClick={handleCloseSnackbar}>
              <CloseIcon fontSize="small" />
            </IconButton>
          </React.Fragment>
        }
        sx={{
          backgroundColor: snackbar.severity === 'success' ? 'rgba(76, 175, 80, 0.9)' : 'rgba(244, 67, 54, 0.9)',
          position: 'absolute',
          bottom: 20,
          left: 20,
        }}
      />
    </HomeContainer>
  );
};

export default HomePage;